import React, { FC, HTMLAttributes, useCallback, useEffect, useState } from 'react';
import { useCookies } from 'react-cookie';

//styles
import './styles.scss';
//components
import { Btn } from '../Button';
import { InputText } from '../Input';
//img
import { logoSkel } from '../../assets/img';

interface NewsletterProps extends HTMLAttributes<HTMLDivElement> { }

export const Newsletter: FC<NewsletterProps> = () => {
  const [show, setShow] = useState<boolean>(false);
  const [cookies, setCookie] = useCookies(['name']);

  const onHide = () => setShow(false)
  const onShow = () => setShow(true)

  useEffect(() => {
    if (cookies.name === 'newsletter') return () => { }

    const timer = setTimeout(() => {
      onShow()
    }, 3000);
    setShowCookie()

    return () => clearTimeout(timer);
  }, [])

  const setShowCookie = () => {
    setCookie('name', 'newsletter', { maxAge: 600 });
  }

  return (
    <div className={`newsletter ${show ? "newsletter__show" : "newsletter__hide"}`}>
      <div className='newsletter__overlay' onClick={onHide}></div>
      <div className='newsletter__panel'>
        <div className='text'>
          <img className='newsletter__panelImg' src={logoSkel} />
          <p className='newsletter__panelText'>
            Dive into Exclusive Real Estate Insights and Networking Opportunities! 
          </p>
          <p className='newsletter__panelText'>
            Join the Rising Tides community and elevate your journey with expert insights, market trends, and valuable networking event reminders.
          </p>
          <p className='newsletter__panelText'>
             What's in store for you:
          </p>
          <p className='newsletter__panelText'>
             In-depth Real Estate Analysis: Stay ahead with comprehensive market insights and investment strategies.
          </p>
          <p className='newsletter__panelText'>
             Network, Connect, Succeed: Never miss a chance to expand your circle and learn from fellow investors.
          </p>
          <p className='newsletter__panelText'>
             Event Reminders: Be the first to know about upcoming workshops, webinars, and meetups.
          </p>
        </div>
        <InputText />
        <Btn
          text='Subscribe'
        />
      </div>
    </div>
  );
};

